@import "../../colors.sass";

.employees {
	margin-top: 40px;
	justify-content: center;

	.employeeWrapper {
		padding: 15px 15px; }

	.employeeCard {
		height: auto;
		box-shadow: 3px 3px 17px 0 rgba(0, 0, 0, 0.4), 3px 3px 8px 0 rgba(0, 0, 0, 0.19);
		border-radius: 5px;
		border: $lightGrey;
		background: $darkSlate;
		color: white;
		&.darkMode {
			color: $darkSlate;
			background: white; }

		.portrait {
			border-radius: 5px 5px 0 0;
			padding: 0;
			margin: 0;
			object-fit: cover;
			object-position: top;
			width: 100%;
			height: 250px;
			.colorOverlay {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				height: 100%;
				width: 100%;
				opacity: 0.8;
				background-color: $yellow; } }
		.info {
			padding: 15px;
			height: 100px;
			border-radius: 0 0 5px 5px;
			label {
				font-weight: 600; }
			.started {
				color: $lightGrey;
				font-size: 14px; }
			.role {
				margin-top: 10px;
				font-size: 16px;
				font-weight: bold;
				color: $yellow;
				text-align: center;
				border-top: 1px solid $yellow;
				padding-top: 5px; } } } }
