@import url(https://fonts.googleapis.com/css?family=Lato:100,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  font-family: 'Lato';
  color: #5a5e65; }

h2 {
  font-size: 30px; }

.paragraph {
  margin-top: 40px;
  color: #5a5e65;
  font-size: 18px;
  text-align: justify;
  line-height: 25px; }

.darkMode p {
  color: white; }

.topHero {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  min-height: 30em;
  direction: ltr;
  background-position: center; }
  .topHero .text-wrap {
    margin-top: auto;
    width: 100%; }
    .topHero .text-wrap h1 {
      text-shadow: 2px 2px 8px black;
      text-transform: uppercase;
      color: white;
      word-wrap: break-word;
      font-size: 40px; }

@media (min-width: 1200px) {
  .topHero {
    min-height: 35em; }
    .topHero .text-wrap h1 {
      font-size: 60px; } }

@media (min-width: 1500px) {
  .topHero {
    min-height: 45em; } }

@media (min-width: 1920px) {
  .topHero {
    min-height: 56em; } }

@media (max-width: 576px) {
  .topHero .text-wrap h1 {
    margin: 20px 30px;
    font-size: x-large; } }

.intro {
  color: #5a5e65; }
  .intro .paragraph {
    margin-top: 40px;
    color: #5a5e65;
    font-size: 18px;
    text-align: justify;
    line-height: 25px; }
  .intro .projects {
    margin-top: 80px;
    text-align: center; }
  .intro .project {
    margin-bottom: 30px; }
    .intro .project p {
      font-size: 18px;
      text-align: center; }

.darkMode .intro {
  color: white; }

.anchorBar {
  display: flex;
  z-index: 1;
  flex-direction: row;
  direction: rtl;
  align-items: center;
  width: 100%;
  height: 5em;
  -webkit-transition: height 0.2s ease-in-out;
  transition: height 0.2s ease-in-out;
  background-color: #ffc843; }
  .anchorBar.sticky {
    height: 3em;
    box-shadow: 0px -10px 20px 5px rgba(0, 0, 0, 0.8); }
  .anchorBar .anchorList {
    min-width: 50%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap-reverse;
    justify-content: space-evenly;
    align-items: center;
    list-style-type: none;
    font-size: medium; }
    .anchorBar .anchorList .anchorItem {
      font-weight: 700;
      color: #5a5e65; }
      .anchorBar .anchorList .anchorItem a {
        text-decoration: none;
        color: #5a5e65; }
    .anchorBar .anchorList .slash {
      color: white;
      font-size: x-large;
      font-weight: 400; }

@media (max-width: 576px) {
  .anchorBar.sticky {
    height: 3.5em; }
  .anchorBar .anchorList {
    min-width: 100%;
    padding: 0 15px;
    font-size: small; }
    .anchorBar .anchorList .anchorItem {
      padding-top: 3px;
      padding-bottom: 3px; }
    .anchorBar .anchorList .slash {
      display: none; } }

.sectionWrapper {
  position: relative;
  width: 100%;
  display: block;
  overflow: auto;
  padding-top: 60px;
  padding-bottom: 60px; }
  .sectionWrapper .headingWrapper .sectionHeading {
    margin-top: 0;
    font-size: 60px;
    margin-bottom: 0;
    word-wrap: break-word; }
  .sectionWrapper .headingWrapper .sectionSubheading {
    margin-top: 0;
    font-size: 20px;
    word-wrap: break-word; }
  .sectionWrapper p {
    line-height: 22px;
    text-align: left; }

@media (max-width: 576px) {
  .sectionWrapper .headingWrapper {
    text-align: left !important; }
    .sectionWrapper .headingWrapper .sectionHeading {
      font-size: xx-large; } }

.employees {
  margin-top: 40px;
  justify-content: center; }
  .employees .employeeWrapper {
    padding: 15px 15px; }
  .employees .employeeCard {
    height: auto;
    box-shadow: 3px 3px 17px 0 rgba(0, 0, 0, 0.4), 3px 3px 8px 0 rgba(0, 0, 0, 0.19);
    border-radius: 5px;
    border: #bababa;
    background: #5a5e65;
    color: white; }
    .employees .employeeCard.darkMode {
      color: #5a5e65;
      background: white; }
    .employees .employeeCard .portrait {
      border-radius: 5px 5px 0 0;
      padding: 0;
      margin: 0;
      object-fit: cover;
      object-position: top;
      width: 100%;
      height: 250px; }
      .employees .employeeCard .portrait .colorOverlay {
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 100%;
        width: 100%;
        opacity: 0.8;
        background-color: #ffc843; }
    .employees .employeeCard .info {
      padding: 15px;
      height: 100px;
      border-radius: 0 0 5px 5px; }
      .employees .employeeCard .info label {
        font-weight: 600; }
      .employees .employeeCard .info .started {
        color: #bababa;
        font-size: 14px; }
      .employees .employeeCard .info .role {
        margin-top: 10px;
        font-size: 16px;
